@mixin line-clamp($numLines : 5, $lineHeight: 1.3) {
	overflow: hidden;
	text-overflow: -o-ellipsis-lastline;
	text-overflow: ellipsis;
	display: block;
	/* autoprefixer: off */
	display: -webkit-box;
	-webkit-line-clamp: $numLines;
	-webkit-box-orient: vertical;
	max-height: $numLines * $lineHeight + unquote('em');
	// вставлять как-то так: @include line-clamp(6); 6 - 6 строк
}
@mixin placeholder {
	&.placeholder {
		@content;
	}
	&:-moz-placeholder {
		@content;
	}
	&::-moz-placeholder {
		@content;
	}
	&:-ms-input-placeholder {
		@content;
	}
	&::-webkit-input-placeholder {
		@content;
	}
}
@mixin text-decoration {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 0%;
	content: '.';
	color: transparent;
	background: #989b9d;
	height: 1px;
	transition: all 0.4s;
}
* {
	box-sizing: border-box;
	outline: none !important;
}
/* reset */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
}
ul {
	margin-top: 0;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
	display: block;
}
blockquote,
q {
	quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-width: 0px;
	padding: 0px;
	margin: 0px;
}
html {
	font-size: 100%;
	font-size: 1rem;
	line-height: $base-line-height;
	-webkit-text-size-adjust: none;
	-ms-text-size-adjust: none;
	height: 100%;
	&.mm-opened {
		height: auto;
	}
}
body {
	color: $text-color;
	margin: 0px;
	padding: 0px;
	font-family: $base-font-family;
	background-color: $base-bg;
	font-size: $base-font-size;
	height: 100%;
	line-height: $base-line-height;
	position: relative;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
input,
textarea {
	color: #333;
	font-family: $base-font-family;
	outline: none;
	border-radius: 0;
	-moz-border-radius: 0;
	-webkit-border-radius: 0;
}
input[type="button"],
input[type="submit"],
button {
	cursor: pointer
}
td {
	margin: 0px;
	padding: 0px;
}
form {
	padding: 0px;
	margin: 0px;
}
a,
input[type=submit],
input[type=button],
button {
	-webkit-appearance: none;
	outline: none !important;
}
* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
*:before,
*:after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
.clearfix:after,
.wrapper:after,
.row:after {
	content: ".";
	display: block;
	height: 0;
	clear: both;
	visibility: hidden;
}
a {
	color: $link-color;
	text-decoration: underline;
	outline: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	&:visited,
	&:hover {
		text-decoration: none;
		color: $link-color;
	}
	&:active,
	&:focus {
		outline: none;
	}
	&:focus {
		text-decoration: none;
	}
	&:hover {
		color: $link-color-hover;
	}
}
a:active {
	background-color: transparent;
}
textarea,
input[type=url],
input[type=search],
input[type=number],
input[type=password],
input[type=tel],
input[type=text],
input[type=email] {
	height: 32px;
	border: 1px solid #ccc;
	background-color: rgba(255, 255, 255, .2);
	margin: 0;
	font: 16px/30px $base-font-family;
	color: $text-color;
	padding: 6px 15px;
	vertical-align: middle;
	display: inline-block;
	@include placeholder {
		color: #c7c7c7;
	}
}
textarea {
	width: 222px;
	height: 102px;
	font: 16px/20px $base-font-family;
	overflow: auto;
}
@keyframes pulsemy {
	from {
		box-shadow: transparentize(#000, .9) 0 0 4px 0, transparentize(#000, 1) 0 0 8px 35px;
	}
	to {
		box-shadow: transparentize(#000, 1) 0 0 8px 50px, transparentize(#000, .9) 0 0 4px 0;
	}
}
@keyframes pulsemy-hover {
	from {
		box-shadow: transparentize(#000, .8) 0 0 4px 0, transparentize(#000, 1) 0 0 8px 35px;
	}
	to {
		box-shadow: transparentize(#000, 1) 0 0 8px 80px, transparentize(#000, .9) 0 0 4px 0;
	}
}
@keyframes pulsemy-none {
	from {
		box-shadow: transparentize(#000, .8) 0 0 0 0, transparentize(#000, 1) 0 0 0 0;
	}
	to {
		box-shadow: transparentize(#000, 1) 0 0 0 0, transparentize(#000, .9) 0 0 0 0;
	}
}
// фон автозаполненных полей
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 0 white inset !important;
}
// цвет автозаполнения
input:-webkit-autofill {
    -webkit-text-fill-color: #bdbdbd !important;
}
button,
.button,
a.button,
input[type=button],
input[type=submit] {
	height: 51px;
	border: 1px solid #ffe735;
	border-radius: 25px;
	background-color: #ffe735;
	font: 400 18px/49px $base-font-family;
	transition: .2s;
	margin: 0;
	padding: 0 37px;
	color: #000;
	vertical-align: middle;
	display: inline-block;
	cursor: pointer;
	text-decoration: none;
	text-align: center;
	text-transform: uppercase;
	&:hover {
		background: #fff;
		text-decoration: none;
	}
	&.inverse {
		background: #fff;
		text-decoration: none;
		&:hover {
			background-color: #ffe735;
		}
	}
}
::-webkit-input-placeholder {
	opacity: 1;
}
:-ms-input-placeholder {
	opacity: 1;
}
::-moz-placeholder {
	opacity: 1;
}
:focus::-webkit-input-placeholder {
	color: transparent !important;
}
:focus:-ms-input-placeholder {
	color: transparent !important;
}
:focus::-moz-placeholder {
	color: transparent !important;
}
textarea:invalid,
input:invalid {
	box-shadow: none;
}
