//main styles
.wrapper {
	min-width: $min-width;
	max-width: $max-width;
	padding: 0 15px;
	margin: 0 auto;
	position: relative;
}
p {
	padding: 0 0 15px;
}
/* titles start */
h1 {
	font-family: $custom-font-family;
	font-size: $font-size-h1;
	line-height: $line-height-h1;
	padding: 0 0 15px;
	color: $title-color;
	letter-spacing: 2px;
}
h2 {
	font-family: $custom-font-family;
	font-size: $font-size-h2;
	line-height: $line-height-h2;
	padding: 0 0 15px;
	color: $title-color;
	font-weight: 300;
	letter-spacing: 2px;
}
h3 {
	font-family: $custom-font-family;
	font-size: $font-size-h3;
	line-height: $line-height-h3;
	color: $title-color;
	padding: 0 0 15px;
}
h4,
h5,
h6 {
	font-weight: 700;
	padding: 0 0 15px;
	color: $title-color;
}
/* titles end */
.custom2 {
	font-family: $custom2-font-family;
}
/* Styles for dialog window */
.small-popup {
	background: #fff;
	padding: 20px 30px;
	text-align: left;
	max-width: 400px;
	margin: 40px auto;
	position: relative;
}
.mfp-with-zoom .mfp-container,
.mfp-with-zoom.mfp-bg {
	opacity: 0;
	-webkit-backface-visibility: hidden;
	/* ideally, transition speed should match zoom duration */
	-webkit-transition: all 0.3s ease-out;
	-moz-transition: all 0.3s ease-out;
	-o-transition: all 0.3s ease-out;
	transition: all 0.3s ease-out;
}
.mfp-with-zoom.mfp-ready .mfp-container {
	opacity: 1;
}
.mfp-with-zoom.mfp-ready.mfp-bg {
	opacity: 0.8;
}
.mfp-with-zoom.mfp-removing .mfp-container,
.mfp-with-zoom.mfp-removing.mfp-bg {
	opacity: 0;
}
/* Styles for dialog window end */
.mm-opened .mm-slideout {
	cursor: pointer;
}
.mm-title img {
	height: 20px;
}
.slick-arrow {
	position: absolute;
	top: 32%;
	border: none;
	cursor: pointer;
	padding: 0;
	width: 24px;
	height: 24px;
	z-index: 3;
	&.slick-prev {
		left: 10px;
	}
	&.slick-next {
		right: 10px;
	}
}
.mm-page,
#my-page {
	height: 100%;
}
.icon {
	display: inline-block;
}
/* loader */
.loaded .main-wrapper {
	visibility: hidden;
}
.icon-load {
	/*background: url(../img/loader.svg) no-repeat left top;*/
	width: 100px;
	height: 100px;
	position: fixed;
	left: 50%;
	top: 50%;
	transform: translateX(-50%) translateY(-50%);
	display: none;
	img {
		width: 100%;
		height: 100%;
	}
}
.loaded .icon-load {
	display: block;
}
.hidden-block {
	display: none;
}
.main-wrapper {
	padding: 0;
	min-width: $min-width;
	width: 100%;
	position: relative;
	min-height: 100%;
	display: flex;
	flex-direction: column;
	display: flex;
	flex-flow: column nowrap;
	justify-content: space-around;
}
.header {
	height: 100vh;
	min-height: 800px;
	background: url(../img/header-bg.jpg) no-repeat center / cover;
	width: 100%;
	min-width: $min-width;
	text-align: left;
	order: 1;
	overflow: hidden;
	color: #fff;
	.wrapper {
		position: relative;
		height: calc(100% - 273px);
	}
	a {
		color: #fff;
	}
	&__top {
		padding: 50px 68px 46px 60px;
		height: 273px;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		align-items: flex-start;
		.buttonbox {
			padding: 13px 0;
			.button {
				background-color: transparent;
				color: #fff;
				&:hover {
					color: #000;
				}
			}
		}
	}
	.logo {
		a {
			display: block;
			color: #fff;
		}
		span,
		img {
			display: inline-block;
			vertical-align: middle;
		}
		span {
			font: bold 21px 'Gilroy', Arial, sans-serif;
			padding: 8px 0 0 18px;
			text-transform: uppercase;
		}
	}
	&__content {
		display: inline-block;
		position: relative;
	}
	h1 {
		color: #fff;
		font: 60px/87px 'Gilroy', Arial, sans-serif;
		text-transform: uppercase;
		letter-spacing: 7px;
		padding: 0 0 19px;
		strong {
			font-weight: bold;
			color: $link-color;
			font-size: 80px;
			display: block;
			letter-spacing: 10px;
		}
	}
	&__text {
		border-top: 2px solid rgba(255, 255, 255, .2);
		padding-left: 32px 0;
		padding: 18px 0;
	}
	p {
		font: 300 24px/34px 'Gilroy', Arial, sans-serif;
		padding: 0 0 38px;
		b,
		strong {
			color: $link-color;
		}
	}
	&__bottom {
		background-color: #fff;
		position: absolute;
		height: 258px;
		width: calc(100% + 92px);
		left: -46px;
		bottom: 0;
		border-radius: 44px 44px 0 0;
		padding: 40px 46px 0;
		overflow: hidden;
	}
}
.hb_calc {
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	align-items: flex-start;
	color: $title-color;
	>div {
		width: 326px;
	}
	label {
		font: 300 18px 'Gilroy', Arial, sans-serif;
		color: $text-color;
		padding-bottom: 10px;
		display: block;
	}
	.num {
		font: 700 32px 'Gilroy', Arial, sans-serif;
		padding-bottom: 18px;
		span {
			font-size: 24px;
		}
	}
}
.slider_move {
	background: #d5d9dd;
	position: relative;
	margin: 0 auto;
	width: 100%;
	height: 2px;
}
.ui-slider-handle {
	width: 28px;
	height: 28px;
	position: absolute;
	top: -12px;
	margin-left: -14px;
	z-index: 200;
	background: $link-color;
	-webkit-border-radius: 50px;
	-moz-border-radius: 50px;
	border-radius: 50px;
	cursor: pointer;
	&:active {
		background-color: $link-color;
	}
	&:after {
		content: '';
		display: block;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		border: 1px solid #fff;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
	}
}
.ui-widget-header {
	background: $link-color;
	height: 4px;
	left: 1px;
	top: -1px;
	position: absolute;
}
.results {
	&__table {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	&__cell {
		width: 50%;
		padding: 7px 7px 7px 14px;
		border: 1px solid #d5d9dd;
		&:nth-child(1) {
			border-top: none;
			border-left: none;
		}
		&:nth-child(2) {
			border-top: none;
			border-right: none;
			border-left: none;
		}
		&:nth-child(3) {
			border-bottom: none;
			border-left: none;
			border-top: none;
		}
		&:nth-child(4) {
			border: none;
		}
	}
	.label {
		font: 11px/18px 'Gilroy', Arial, sans-serif;
		color: $text-color;
	}
	&__text {
		font: bold 12px/18px 'Gilroy', Arial, sans-serif;
	}
}
.hb__button {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	a {
		display: block;
		background-color: $link-color;
		color: $title-color;
		text-align: center;
		text-decoration: none;
		height: 74px;
		transition: .2s;
		&:hover {
			background-color: rgb(254, 231, 72);
			box-shadow: 0 0 16px rgba(254, 231, 72, .8);
		}
	}
	span {
		display: inline-block;
		font: 21px/78px 'Gilroy', Arial, sans-serif;
		text-transform: uppercase;
		position: relative;
		padding-right: 50px;
		&:after {
			content: '';
			display: block;
			width: 31px;
			height: 27px;
			background: url(../img/link-arrow.png) no-repeat;
			position: absolute;
			right: 0;
			top: 23px;
		}
	}
}
.art {
	position: absolute;
	width: 130px;
	height: 130px;
	border-radius: 50%;
	border: 1px solid rgba(255, 255, 255, .3);
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	top: 26px;
	left: 100%;
	margin-left: 40px;
	&:after {
		content: '';
		display: block;
		width: 98px;
		height: 98px;
		border: 2px solid $link-color;
		border-radius: 50%;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
		opacity: .5;
	}
}
.percent {
	position: absolute;
	right: 0;
	top: 130px;
	width: 280px;
	&__num {
		color: $link-color;
		font: 200px/1 'Gilroy', Arial, sans-serif;
	}
	sup {
		font-size: 70px;
		position: relative;
		top: -25px;
		left: -5px;
	}
	&__text {
		position: absolute;
		font: 21px/34px 'Gilroy', Arial, sans-serif;
		bottom: 26px;
		left: 126px;
	}
}
.content {
	min-width: $min-width;
	text-align: left;
	width: 100%;
	order: 2;
	flex: 1;
}
section {
	padding: 50px 0;
	h2 {
		text-align: center;
	}
	.subtitle {
		font: 300 23px 'Gilroy', Arial, sans-serif;
		text-align: center;
		padding: 24px 0 70px;
		border-top: 1px solid #d5d9dd;
	}
}
#how_working {
	padding: 77px 0 56px;
	background-color: #f5f5f6;
}
.hw_steps {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	border-right: 2px solid #000;
	.step {
		width: 25%;
		border-left: 2px solid #000;
		padding: 18px 42px 28px 50px;
		position: relative;
		&:after {
			content: '';
			display: block;
			width: 30px;
			height: 50px;
			background: url(../img/hw-corner.png) no-repeat;
			position: absolute;
			left: -2px;
			top: 50%;
			transform: translateY(-50%);
		}
		&:first-child {
			&:after {
				display: none;
			}
		}
	}
	&__top {
		display: flex;
		justify-content: space-between;
		flex-wrap: nowrap;
		padding-bottom: 36px;
	}
	.title {
		font: 20px/24px 'Gilroy', Arial, sans-serif;
		color: $title-color;
		padding-top: 6px;
	}
	.num_box {
		width: 70px;
		flex: none;
		text-align: right;
	}
	p {
		line-height: 20px;
		min-height: 80px;
	}
	.ico_box {
		text-align: center;
	}
}
.footer {
	background: #ff0;
	min-height: 80px;
	width: 100%;
	min-width: $min-width;
	order: 3;
	padding: 15px;
	text-align: left;
}